export default {
	data() {
		return {
			name:'',
			data:{},
			height:"",
			swiperheight:558,
			scale:10,
			background:{
				backgroundImage: "linear-gradient(90deg, #54C3F1, #00ABEB)",
			},
			id:0, // 使用 marker点击事件 需要填写id
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			covers: [{
				latitude: 39.909,
				longitude: 116.39742,
				iconPath: '../../static/wait-list/location.png',
				width:30,
				height:30,
				
			}, {
				latitude: 39.90,
				longitude: 116.39,
				iconPath: '../../static/wait-list/location1.png',
				width:30,
				height:30,
			}]
		}
	},
	onLoad(event) {
		// TODO 后面把参数名替换成 payload
		const payload = event.detailDate;
		// 目前在某些平台参数会被主动 decode，暂时这样处理。
		try {
			this.data= JSON.parse(decodeURIComponent(payload));
			console.log("详情页参数");
			console.log(this.data);
			let loction = this.data.longitude.split(",");
			console.log(loction);
			this.latitude = loction[1];
			this.longitude  =loction[0];
			this.covers[0].latitude = loction[1];
			this.covers[0].longitude  =loction[0];
			this.covers[1].longitude=uni.getStorageSync('longitude');
			this.covers[1].latitude=uni.getStorageSync('latitude');
		} catch (error) {
			this.data = JSON.parse(payload);
			console.log("详情页参数");
			console.log(this.data);
		};
		this.name = uni.getStorageSync("name");
		// uni.getSystemInfo({
		// 	success: (res) => {
		// 		// console.log(res.windowHeight);
		// 		let height = res.windowHeight - uni.upx2px(600);
		// 		this.swiperheight = height;
		// 		console.log(this.swiperheight);
		// 	}
		// });
	},
	methods: {
		navlociton(){
			let that  = this;
			console.log(that.latitude);
			wx.openLocation({
				latitude:Number(that.latitude) ,
				longitude: Number(that.longitude) ,
				success() {
					console.log("成功");
				}
			})
		},
		//拨打客服电话
		go(){
		 	uni.makePhoneCall({
		 	
		 	// 手机号
		    phoneNumber: '400-0015-021', 
		
			// 成功回调
			success: (res) => {
				console.log('调用成功!')	
			},
		
			// 失败回调
			fail: (res) => {
				console.log('调用失败!')
			}
			
		  });
		},
		// complate(){
		// 	uni.showModal({
		// 	    title: '提示',
		// 	    content: '订单已完成,不可点击',
		// 	    success: function (res) {
		// 	        if (res.confirm) {
		// 	            console.log('用户点击确定');
		// 	        } else if (res.cancel) {
		// 	            console.log('用户点击取消');
		// 	        }
		// 	    }
		// 	});
		// },
		start(){
			let that = this;
			uni.showModal({
				  title: '提示',
				    content: '订单完成后可到订单中心查看结款状态',
				    success: function (res) {
				        if (res.confirm) {
				            console.log('用户点击确定');
							that.getAccomplish(that.data.code);
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
			})
		},
		//拉去用户订单完成信息
		getAccomplish(code){
			uni.request({
				url:this.$apiUrl+"work/accomplish",
				method:"POST",
				dataType:JSON,
				data:{
					uid:uni.getStorageSync("uid"),
					code:code,
				},
				success(res) {
					console.log("完成订单");
					console.log(res);
					let data = JSON.parse(res.data);
					console.log(data);
					switch (data.code){
						case 200:
						setTimeout(()=>{
							uni.navigateBack({
							    delta: 1
							});
						},1500);
							break;
						case 300:
						uni.showToast({
							title:"该订单已经完成了",
							duration:2000
						})
							break;
						default:
							break;
					}
					
					
				},
				fail(res) {
					console.log(res);
					uni.showToast({
						title:"服务器无响应"
					})
				}
			})
		}
	},
	//自定义分享页面
	onShareAppMessage(e){
		return {
			title: this.$overShare.title,
			path: this.$overShare.path,
			imageUrl:this.$overShare.imageUrl,
			
		}
	}
}
